			// 点击编辑时触发
			editRow(index, row) {
				this.drawer = true
				this.getDisabled = false
				this.drawerTitle = '编辑'
				var rowObj = Object.assign({}, row) //
				if(rowObj.shipmentStatus == 0){
					rowObj.shipmentStatus = '未填写地址'
				}else if(rowObj.shipmentStatus == 1){
					rowObj.shipmentStatus = '待发货'
				}else if(rowObj.shipmentStatus == 4){
					rowObj.shipmentStatus = '现场已兑换'
					this.getDisabled = true
				}else if(rowObj.shipmentStatus == 5){
					rowObj.shipmentStatus = '已过期'
				}
				this.validateForm = rowObj
			},
			handleEditPrize(index, row) {
				this.drawerPrize = true
				this.drawerTitlePrize = '编辑'
				var rowObj = Object.assign({}, row) //
				this.validateFormPrize = rowObj
				var result = this.validateFormPrize.probability1.split("-")
				if (result[1] == 9) {
					this.validateFormPrize.probability1 = parseFloat(this.validateFormPrize.probability1).toFixed(9)
				} else if (result[1] == 8) {
					this.validateFormPrize.probability1 = parseFloat(this.validateFormPrize.probability1).toFixed(8)
				} else if (result[1] == 7) {
					this.validateFormPrize.probability1 = parseFloat(this.validateFormPrize.probability1).toFixed(7)
				} else if (result[1] == 6) {
					this.validateFormPrize.probability1 = parseFloat(this.validateFormPrize.probability1).toFixed(6)
				} else if (result[1] == 5) {
					this.validateFormPrize.probability1 = parseFloat(this.validateFormPrize.probability1).toFixed(5)
				} else if (result[1] == 4) {
					this.validateFormPrize.probability1 = parseFloat(this.validateFormPrize.probability1).toFixed(4)
				} else {
					var arr = parseFloat(this.validateFormPrize.probability1).toFixed(10)
